/* ==============================================
   Global Styles - Tailwind CSS
   ============================================== */

@tailwind base;
@tailwind components;
@tailwind utilities;

/* ==============================================
   Custom Base Styles
   ============================================== */

@layer base {
  body {
    @apply bg-gray-50 text-gray-900;
  }
}

/* ==============================================
   Custom Component Classes
   ============================================== */

@layer components {

  /* ==============================================
     BUTTONS
     ============================================== */

  .btn-primary {
    @apply inline-flex items-center justify-center px-4 py-2.5 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white;
    @apply bg-gradient-to-r from-primary-600 to-primary-700;
    @apply transition-all duration-200 ease-out;
    @apply hover:from-primary-700 hover:to-primary-800 hover:shadow-lg hover:shadow-primary-500/30;
    @apply active:scale-[0.98];
    @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
    @apply disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:shadow-sm disabled:active:scale-100;
  }

  .btn-secondary {
    @apply inline-flex items-center justify-center px-4 py-2.5 border border-gray-200 rounded-lg shadow-sm text-sm font-medium text-gray-700 bg-white;
    @apply transition-all duration-200 ease-in-out;
    @apply hover:bg-gray-50 hover:text-gray-900 hover:border-gray-300;
    @apply active:bg-gray-100 active:scale-[0.98];
    @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-400;
    @apply disabled:opacity-50 disabled:cursor-not-allowed;
  }

  .btn-danger {
    @apply inline-flex items-center justify-center px-4 py-2.5 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white;
    @apply bg-red-600;
    @apply transition-all duration-200 ease-out;
    @apply hover:bg-red-700 hover:shadow-lg hover:shadow-red-500/30;
    @apply active:scale-[0.98];
    @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500;
    @apply disabled:opacity-50 disabled:cursor-not-allowed;
  }

  .btn-icon {
    @apply p-2 text-gray-400 rounded-lg transition-all duration-200;
    @apply hover:text-primary-600 hover:bg-primary-50;
    @apply active:bg-primary-100;
    @apply focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-1;
  }

  .btn-icon-danger {
    @apply p-2 text-gray-400 rounded-lg transition-all duration-200;
    @apply hover:text-red-600 hover:bg-red-50;
    @apply active:bg-red-100;
    @apply focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-1;
  }

  /* ==============================================
     FORM ELEMENTS
     ============================================== */

  .form-group {
    @apply mb-4;
  }

  .form-label {
    @apply block text-sm font-medium text-gray-700 mb-1.5;
  }

  .input-field,
  .input-premium {
    @apply block w-full px-3 py-2.5 border border-gray-200 rounded-lg shadow-sm text-sm bg-white;
    @apply transition-all duration-200 ease-in-out;
    @apply focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500 focus:outline-none;
    @apply hover:border-gray-300;
    @apply placeholder:text-gray-400;
    @apply disabled:bg-gray-50 disabled:text-gray-500 disabled:cursor-not-allowed;
  }

  .form-error {
    @apply mt-1.5 text-xs text-red-600 flex items-center gap-1;
  }

  .toggle-switch {
    @apply relative inline-flex flex-shrink-0 h-6 w-11 border-2 border-transparent rounded-full cursor-pointer;
    @apply transition-colors ease-in-out duration-200;
    @apply focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
    @apply bg-gray-200;
  }

  .toggle-switch.toggle-active {
    @apply bg-primary-600;
  }

  .toggle-knob {
    @apply pointer-events-none inline-block h-5 w-5 rounded-full bg-white shadow transform ring-0;
    @apply transition ease-in-out duration-200;
    @apply translate-x-0;
  }

  .toggle-switch.toggle-active .toggle-knob {
    @apply translate-x-5 rtl:-translate-x-5;
  }

  /* ==============================================
     TABLES
     ============================================== */

  .table-container {
    @apply overflow-hidden rounded-xl border border-gray-200 bg-white;
  }

  .data-table {
    @apply min-w-full divide-y divide-gray-200;
  }

  .table-header {
    @apply px-4 py-3 text-start text-xs font-semibold text-gray-500 uppercase tracking-wider bg-gray-50/80;
  }

  .table-row {
    @apply bg-white transition-colors duration-150;
    @apply hover:bg-gray-50/50;
  }

  .table-row:nth-child(even) {
    @apply bg-gray-50/30;
  }

  .table-cell {
    @apply px-4 py-3.5 text-sm text-gray-700 whitespace-nowrap;
  }

  /* ==============================================
     FILTERS
     ============================================== */

  .filter-bar {
    @apply flex flex-wrap items-center gap-3 p-4 bg-white rounded-xl border border-gray-200;
  }

  .filter-search {
    @apply relative flex-1 min-w-[200px];
  }

  .filter-search-icon {
    @apply absolute start-3 top-1/2 -translate-y-1/2 h-4 w-4 text-gray-400;
  }

  .filter-search-input {
    @apply w-full ps-10 pe-4 py-2 border border-gray-200 rounded-lg text-sm;
    @apply placeholder:text-gray-400;
    @apply focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500 focus:outline-none;
  }

  .filter-select {
    @apply px-3 py-2 border border-gray-200 rounded-lg text-sm text-gray-700 bg-white;
    @apply focus:ring-2 focus:ring-primary-500/20 focus:border-primary-500 focus:outline-none;
    @apply cursor-pointer min-w-[140px];
  }

  /* ==============================================
     PAGE HEADERS
     ============================================== */

  .page-header {
    @apply flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-6;
  }

  .page-title {
    @apply text-2xl font-bold text-gray-900 tracking-tight;
  }

  .page-subtitle {
    @apply text-sm text-gray-500 mt-1;
  }

  .page-actions {
    @apply flex items-center gap-3;
  }

  /* ==============================================
     BADGES & STATUS
     ============================================== */

  .badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }

  .badge-success {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
    @apply bg-green-50 text-green-700 border border-green-200;
  }

  .badge-danger {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
    @apply bg-red-50 text-red-700 border border-red-200;
  }

  .badge-warning {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
    @apply bg-amber-50 text-amber-700 border border-amber-200;
  }

  .badge-info {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
    @apply bg-blue-50 text-blue-700 border border-blue-200;
  }

  .badge-neutral {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
    @apply bg-gray-100 text-gray-600 border border-gray-200;
  }

  /* ==============================================
     CARDS
     ============================================== */

  .card {
    @apply bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden;
  }

  .card-header {
    @apply px-5 py-4 border-b border-gray-100 bg-gray-50/50;
  }

  .card-body {
    @apply p-5;
  }

  .card-title {
    @apply text-lg font-semibold text-gray-900;
  }

  .card-hover {
    @apply transition-all duration-300 ease-out;
    @apply hover:shadow-lg hover:shadow-gray-200/50 hover:border-primary-200;
  }

  /* Glassmorphism */
  .glass {
    @apply bg-white/80 backdrop-blur-md border border-white/20 shadow-sm;
  }

  /* ==============================================
     EMPTY STATE
     ============================================== */

  .empty-state {
    @apply flex flex-col items-center justify-center py-16 text-center;
  }

  .empty-state-icon {
    @apply h-16 w-16 text-gray-300 mb-4;
  }

  .empty-state-title {
    @apply text-lg font-semibold text-gray-900 mb-1;
  }

  .empty-state-desc {
    @apply text-sm text-gray-500 mb-6 max-w-sm;
  }

  /* ==============================================
     LOADING STATES
     ============================================== */

  .loading-spinner {
    @apply animate-spin h-5 w-5 text-primary-600;
  }

  .skeleton {
    @apply bg-gray-200 rounded animate-pulse;
  }

  /* ==============================================
     MODAL
     ============================================== */

  .modal-overlay {
    @apply fixed inset-0 bg-gray-900/50 backdrop-blur-sm z-40;
  }

  .modal-container {
    @apply fixed inset-0 z-50 flex items-center justify-center p-4;
  }

  .modal-content {
    @apply bg-white rounded-xl shadow-xl max-w-lg w-full max-h-[90vh] overflow-y-auto;
  }

}

/* ==============================================
   Custom Utilities & Animations
   ============================================== */

@layer utilities {
  .animate-fade-in-up {
    animation: fadeInUp 0.4s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  }

  .animate-scale-in {
    animation: scaleIn 0.3s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  }

  .animate-pulse-soft {
    animation: pulseSoft 2s infinite ease-in-out;
  }

  /* RTL Support Utilities */
  [dir="rtl"] .rtl\:flip {
    transform: scaleX(-1);
  }

  /* Stagger delays for up to 10 items */
  .delay-100 {
    animation-delay: 100ms;
  }

  .delay-200 {
    animation-delay: 200ms;
  }

  .delay-300 {
    animation-delay: 300ms;
  }

  .animate-fade-in-down {
    animation: fadeInDown 0.2s ease-out forwards;
  }

  /* Hide Scrollbar */
  .scrollbar-hide {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }

  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulseSoft {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.7;
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.animate-shimmer {
  animation: shimmer 2s infinite;
}